import { mapState,mapGetters } from "vuex";
import appIphoneX from '../../../components/basic-component/app-iphone-x/app-iphone-x.vue';
const date = new Date()
const years = []
const months = []
const bigDays = []
const smallDays = []
const secDays = []
const otherDays = []

for (let i = 2015; i <= date.getFullYear(); i++) {
    years.push(i)
}

for (let i = 1; i <= 12; i++) {
    months.push(i)
}

for (let i = 1; i <= 31; i++) {
    bigDays.push(i)
}
for (let i = 1; i <= 30; i++) {
    smallDays.push(i)
}
for (let i = 1; i <= 28; i++) {
    secDays.push(i)
}
for (let i = 1; i <= 29; i++) {
    otherDays.push(i)
}

export default {
    data() {
        return {
            keyword: '',
            timeVal: [],
            time: '',
            start: '',
            end: '',
            years: years,
            months: months,
            days: bigDays,
            bigDays: bigDays,
            smallDays: smallDays,
            secDays: secDays,
            otherDays: otherDays,
            today: '',
            type: 1,
            dialog: false
        }
    },
    components: {
        'app-iphone-x': appIphoneX,
    },
    computed: {
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
    },
    methods: {
        clearSearch() {
            this.keyword = '';
        },
        clearTime(type) {
            if (type == 1) {
                this.start = '';
            } else {
                this.end = ''
            }
        },
        toSearch() {
            let search = {
                keyword: this.keyword,
                dateArr: []
            }
            if (this.start || this.end) {
                search.dateArr = [this.start, this.end]
            }
            this.$storage.setStorage({
                key: "search",
                data: search,
                success(res) {
                    uni.hideLoading();
                    setTimeout(function () {
                        uni.navigateBack();
                    }, 500)
                },
                fail(res) {
                    uni.hideLoading();
                    uni.showToast({
                        title: res.errMsg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            })
        },
        reset() {
            this.keyword = '';
            this.start = '';
            this.end = '';
        },
        submitTime() {
            let start, end;
            let time = new Date(this.time).getTime();
            if (this.type == 1) {
                if (this.end) {
                    end = new Date(this.end).getTime();
                    if (time > end) {
                        uni.showToast({
                            title: '时间区间选择有误，请确认选择的时间',
                            icon: 'none',
                            duration: 1000
                        });
                        return false
                    }
                }
                this.start = this.time;
            } else {
                if (this.start) {
                    start = new Date(this.start).getTime();
                    if (time < start) {
                        uni.showToast({
                            title: '时间区间选择有误，请确认选择的时间',
                            icon: 'none',
                            duration: 1000
                        });
                        return false
                    }
                }
                this.end = this.time
            }
            this.dialog = false;
        },
        timeChange: function (e) {
            const val = +e.detail.value;
            this.timeVal = +e.detail.value;
            let years = this.years;
            let year = this.years[val[0]];
            let month = this.months[val[1]];
            if (month == 2) {
                if (year % 4 == 0 && year % 400 != 0) {
                    this.days = this.otherDays
                } else {
                    this.days = this.secDays
                }
            } else if ((month < 8 && month % 2 == 1) || (month > 7 && month % 2 == 0)) {
                this.days = this.bigDays
            } else {
                this.days = this.smallDays
            }
            let day = this.days[val[2]];
            if (month >= 1 && month <= 9) {
                month = "0" + month;
            }
            if (day >= 1 && day <= 9) {
                day = "0" + day;
            }
            this.time = year + '-' + month + '-' + day;
        },
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.indicatorStyle = 'height: 36px;color:' + that.color + ';font-size:14px;';
        var myDate = new Date();
        // 今天
        let year = myDate.getFullYear();
        let month = myDate.getMonth() + 1;
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        let now = myDate.getDate();
        that.today = year + "-" + month + "-" + now;
        that.time = that.today;
        for (let i in that.years) {
            if (that.today.substring(0, 4) == that.years[i]) {
                that.timeVal[0] = +i
            }
        }
        let chooseMonth = that.today.substring(5, 7);
        for (let i in that.months) {
            if (that.today.substring(5, 7) == that.months[i]) {
                that.timeVal[1] = +i
            }
        }
        if (chooseMonth == 2) {
            if (year % 4 == 0 && year % 400 != 0) {
                this.days = this.otherDays
            } else {
                this.days = this.secDays
            }
        } else if ((chooseMonth < 8 && chooseMonth % 2 == 1) || (chooseMonth > 7 && chooseMonth % 2 == 0)) {
            this.days = this.bigDays
        } else {
            this.days = this.smallDays
        }
        for (let i in that.days) {
            if (that.today.substring(8, 10) == that.days[i]) {
                that.timeVal[2] = +i
            }
        }
        if (this.$storage.getStorageSync('search')) {
            let search = this.$storage.getStorageSync('search');
            that.keyword = search.keyword;
            if (search.dateArr[0]) {
                that.start = search.dateArr[0];
            }
            if (search.dateArr[1]) {
                that.end = search.dateArr[1];
            }
        }
    }
}
